import {
  FidbackBox,
  FidbackImage,
  FidbackInput,
  FidbackSection,
  FidbackText,
  FidbackTitle,
  Form,
  FormWraper,
  // InputText,
} from './FidbackForm.styled';
import formImage from '../../img/hand.png';
